import Util from './Util';
export default {
    BEST_SCORE: "shi-zi-xiao-best-score",//key
    WIDTH: 640,
    getWidth: function () {
        return this.WIDTH;
    },
    getHeight: function () {
        return Util.isPC() ? document.documentElement.clientHeight : document.documentElement.clientHeight * this.WIDTH / document.documentElement.clientWidth;
    },
    flagColor: '#555555',
    girdType0: '#bbbeb0',
    girdType1: '#bbbebf',
    round: 5,//
    gap: 1,//
    totalEl: 7,//el,
    dense: 0.8,//
    helperNum: 3,//
    countTime: 60000,//
    timerBarHeight: 20,//
    scoreBarHeight: 30,//
    scoreScale: 2,//
    timePunish: 3000,//
    size: 68,//
    keepClickGap: 1200//
};


